<template>
    <form @submit="onSubmit" class="add-form">
        <div class="form-control">
            <label>Task</label>
            <input 
                type="text" 
                v-model="text"
                name="text"
                placeholder="Add Task"
            />
        </div>
        <div class="form-control">
            <label>Date</label>
            <input
                type="date"
                v-model="date"
                name="date"
            />
        </div>
        <div class="form-control">
            <label>Time</label>
            <input
                type="time"
                v-model="time"
                name="time"
            />
        </div>
        <div class="form-control form-control-check">
            <label>Set Reminder</label>
            <input
                type="checkbox"
                v-model="reminder"
                name="reminder"
            />
        </div>

        <input 
            type="submit"
            value="Save Task"
            class="btn btn-block"
        />
    </form>
</template>

<script>
    export default {
        name: 'AddTask',
        data() {
            return {
                text: '',
                date: '',
                time: '',
                reminder: false,
            }
        },
        methods: {
            onSubmit(e) {
                e.preventDefault();

                if(!this.text) {
                    alert('Please add a task')
                    return
                }

                const newTask = {
                    // id: Math.floor(Math.random() * 100000),
                    text: this.text,
                    date: this.date,
                    time: this.time,
                    reminder: this.reminder
                }

                this.$emit('add-task', newTask)

                // Clear the form after submission
                this.text = ''
                this.date = ''
                this.time = ''
                this.reminder = false
            }
        }
    }
</script>

<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
